body{
  background-color: #23252C;
  color: white;
  text-align: center;
}

.card{
  width: 333px;
  margin: 50px
   auto;
  background-color: #1A1B21;
  border-radius: 10px;
}

.avatar{
  width: 100%;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}

h1{
  font-size: 1.7rem;
  margin-top: 0.5em;
  margin-bottom: 0;
}

h2{
  font-size: 1.2rem;
  margin-top: 1em;
  margin-bottom: 0.2em;
}

h3{
  color: #F3BF99;
  font-size: 1rem;
  font-weight: lighter;
  margin-top: 0;
  margin-bottom: 0.2em;
}

.email {

  background-color: white;
  color: black;
}

.linkedin {
  background-color: #5093E2;
  color: white;
}

button {
  margin: 16px 8px 6px;
  width: 120px;
  padding: 10px 30px;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  cursor: pointer;
}

.info p{
  font-weight: lighter;
  font-size: 0.75rem;
}

p{
  margin: 0;
  font-size: 0.7rem;
  line-height: 1.1rem;
}

.about p{
  color: #DCDCDC;
}

.about{
  text-align: left;
  padding: 0 2.5em;
}

.about :last-child{
  margin-bottom: 30px;
}

.footer{
  background-color: #161619;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.footer img{
  margin: 15px;
  cursor: pointer;
}
